.options {
  display: flex;
  flex-direction: column;
  background-color: var(--seashell-peach);
  border: 1px solid var(--seashell-peach);
  border-radius: 4px;
  gap: 20px;
  padding: 20px;
  &__title {
    margin: 0;
    max-width: 240px;
    font-weight: 400;
    font-size: 24px;
    line-height: 24px;
    color: var(--river-bed);
  }
  &__item {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  &__head {
    margin: 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    color: var(--river-bed);
  }
  &__wrapper {
    display: flex;
  }
  &__btn {
    padding: 22px 5px;
    font-family: "Intro Regular";
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    color: var(--light);
    background-color: var(--river-bed);
  }
  &__btn--reset {
    background-color: transparent;
    color: var(--river-bed);
  }
}
.size {
  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
  &__item {
    border-collapse: collapse;
    border: 1px solid var(--cameo);
  }
  &__btn {
    width: 100%;
    padding: 20px 5px;
    font-size: 16px;
    line-height: 16px;
    display: flex;
    justify-content: center;
    text-align: center;
    color: var(--river-bed);
  }
}
.options-range {
  &__wrapper {
    display: flex;
    position: relative;
    margin-bottom: 7px;
    &::after {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      width: 1px;
      height: 13px;
      background-color: var(--gray);
      transform: translate(-50%, -50%);
    }
  }
  &__input {
    border: 1px solid var(--gray);
    background-color: transparent;
    border-bottom: none;
    width: 100%;
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    text-align: center;
    color: var(--river-bed);
    padding: 17px 5px;
    &--1 {
      border-right: none;
    }
    &--2 {
      border-left: none;
    }
  }
}
.noUi-horizontal {
  height: 4px;
}
.noUi-connect {
  background-color: var(--river-bed);
}
.noUi-horizontal .noUi-handle {
  width: 5px;
  height: 20px;
  top: -9px;
  right: -2px;
}
.noUi-handle {
  background-color: var(--river-bed);
  border: 1px solid #fff4ee;
  border-radius: 4px;
  cursor: pointer;
}
.noUi-handle:after,
.noUi-handle:before {
  display: none;
}
