.header {
  background-color: var(--east-bay);
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 15;
  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 0;
    border-bottom: 1px solid rgba(255, 255, 255, 0.5);
    gap: 40px;
  }
  &__logo {
    font-family: "Intro Bold";
    font-weight: 700;
    font-size: 30px;
    line-height: 30px;
    color: var(--light);
  }

  &__menu {
    display: flex;
    align-items: center;
  }
}
.nav {
  &__list {
    display: flex;
    gap: 40px;
  }

  &__link {
    font-weight: 400;
    font-size: 14px;
    line-height: 14px;
    color: var(--light);
  }
}
.cart {
  display: flex;
  align-items: center;
  margin-left: 40px;
  padding-right: 10px;
  position: relative;
  cursor: pointer;

  &__icon {
    margin-left: 10px;
    position: relative;
  }
  &__item {
    fill: var(--light);
    width: 20px;
    height: 19px;
    pointer-events: none;
  }

  &__quantity {
    position: absolute;
    bottom: -2px;
    right: -7px;
    display: block;
    padding: 2px 4px 0 5px;
    background-color: var(--carnation);
    border-radius: 100%;
    font-weight: 400;
    font-size: 14px;
    line-height: 14px;
    color: var(--light);
    transition: opacity 0.4s ease-in-out;
    opacity: 0;
    pointer-events: none;
    &--visible {
      transition: opacity 0.4s ease-in-out;
      opacity: 1;
    }
  }
}
