// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Intro Book", sans-serif;
  --content-width: 1210px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --light: #fff;
  --east-bay: #484283;
  --carnation: #f14f4f;
  --river-bed: #444b58;
  --cameo: #dbbba9;
  --seashell-peach: #fff4ee;
  --gray: #b2b5bb;
  --mine-shaft: #242424;
  --black: #000000;
  --tundora: #4d4d4d;
}
