.hero {
  background-color: var(--east-bay);
  padding: 164px 0 100px 0;

  &__container {
    position: relative;
    &::after {
      content: "SneakMax";
      position: absolute;
      bottom: 0;
      right: 49px;
      font-family: "Intro Bold";
      font-weight: 700;
      font-size: 200px;
      line-height: 200px;
      color: rgba(255, 255, 255, 0.12);
      pointer-events: none;
    }
  }

  &__content {
    max-width: 580px;
    position: relative;
    z-index: 2;
  }

  &__title {
    margin: 0;
    font-family: "Intro Regular";
    font-weight: 400;
    font-size: 36px;
    line-height: 140%;
    color: var(--light);
    margin-bottom: 20px;
  }

  &__text {
    margin: 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 140%;
    color: var(--light);
    margin-bottom: 40px;
  }

  &__btn {
    padding: 22px 47px;
    border-radius: 4px;
    background-color: var(--carnation);
    font-family: "Intro Regular";
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    color: var(--light);
  }
}
