.order {
  width: 580px;

  &__content {
    padding: 40px;
  }

  &__top {
    display: flex;
    justify-content: space-between;
    gap: 30px;
    margin-bottom: 40px;
  }

  &__titile {
    margin: 0;
    font-family: "Intro Bold";
    font-weight: 700;
    font-size: 20px;
    line-height: 20px;
    color: var(--river-bed);
  }

  &__number {
    margin: 0;
    font-weight: 400;
    font-size: 14px;
    line-height: 14px;
    color: var(--gray);
  }

  &__info {
    border: 1px solid #d9d9d9;
    border-radius: 4px;
    padding: 30px;
    margin-bottom: 40px;
  }

  &__descr {
    display: block;
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    color: var(--gray);
    margin-bottom: 20px;
  }

  &__quantity {
    font-family: "Intro Bold";
    font-weight: 700;
    font-size: 16px;
    line-height: 16px;
    color: var(--river-bed);
  }

  &__show {
    display: block;
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    color: var(--river-bed);
    margin-bottom: 17px;
  }
  &__list {
    display: flex;
    flex-direction: column;
    gap: 14px;
  }
  &__form {
    display: flex;
    flex-direction: column;
  }

  &__input {
    display: block;
    width: 100%;
    padding: 19px 20px;
    background: #f7f7f7;
    font-weight: 400;
    font-size: 16px;
    line-height: 140%;
    color: var(--river-bed);
    outline: none;
    margin-bottom: 10px;

    &::placeholder {
      color: var(--gray);
    }
  }

  &__btn {
    margin-top: 20px;
    align-self: flex-start;
    padding: 22px 47px;
    background-color: var(--carnation);
    border-radius: 4px;
    font-family: "Intro Regular";
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    color: var(--light);
  }
}
.order-product {
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__content {
    display: flex;
    align-items: center;
    gap: 10px;
  }

  &__img {
    width: 80px;
    height: 80px;
  }

  &__name {
    margin: 0;
    font-weight: 400;
    font-size: 14px;
    line-height: 14px;
    color: var(--river-bed);
    margin-bottom: 10px;
  }

  &__price {
    margin: 0;
    font-family: "Intro Bold";
    font-weight: 700;
    font-size: 16px;
    line-height: 16px;
    color: var(--river-bed);
  }

  &__delete {
    font-weight: 400;
    font-size: 14px;
    line-height: 14px;
    color: var(--gray);
  }
}
