.catalog {
  padding: 60px 0;
  &__title {
    margin: 0;
    font-family: "Intro Bold";
    font-weight: 700;
    font-size: 30px;
    line-height: 30px;
    color: var(--river-bed);
    margin-bottom: 40px;
  }

  &__content {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 20px;
  }

  &__left {
    grid-column: 3 span;
  }

  &__right {
    grid-column: 9 span;
  }
  &__list {
    display: grid;
    grid-template-columns: repeat(9, 1fr);
    gap: 20px;
  }
  &__item {
    grid-column: 3 span;
  }
}
