.product {
  &__img {
    position: relative;
    margin-bottom: 10px;
  }

  &__btns {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 40px;
    background: rgba(255, 255, 255, 0.6);
    transition: opacity 0.4s ease-in-out;
    opacity: 0;
    &:hover {
      opacity: 1;
    }
  }

  &__btn {
    background-color: var(--river-bed);
    border-radius: 100%;
    width: 80px;
    height: 80px;
    svg {
      fill: var(--light);
      width: 30px;
      height: 30px;
    }
    &--disabled {
      opacity: 0.4;
      pointer-events: none;
    }
  }

  &__title {
    margin: 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    color: var(--river-bed);
    margin-bottom: 10px;
  }

  &__text {
    margin: 0;
    font-family: "Intro Regular";
    font-weight: 400;
    font-size: 20px;
    line-height: 20px;
    color: var(--river-bed);
  }
}
