.custom-checkbox {
  @include custom-checkbox;
  &__content {
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    color: var(--river-bed);
  }
  &:not(:first-child) {
    margin-left: 45px;
  }
}
.custom-checkbox__content::before {
  border: 1px solid var(--cameo);
  border-radius: 4px;
}
.custom-checkbox__content::after {
  background-image: url(../img/check.svg);
}
.custom-checkbox__content::before,
.custom-checkbox__content::after {
  top: 2px;
}
.custom-checkbox__field:focus + .custom-checkbox__content::before {
  outline: none;
  outline-offset: 2px;
}
