.questions {
  padding: 60px 0;
  &__title {
    margin: 0;
    font-family: "Intro Bold";
    font-weight: 700;
    font-size: 30px;
    line-height: 30px;
    color: var(--river-bed);
    margin-bottom: 40px;
  }
  &__row {
    display: flex;
    justify-content: center;
  }
  &__list {
    max-width: 880px;
  }

  &__item {
    padding-top: 30px;
    border-collapse: collapse;
    border-top: 1px solid var(--mine-shaft);
    &:not(:first-child) {
      border-bottom: 1px solid var(--mine-shaft);
    }
  }

  &__btn {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 25px;
  }

  &__name {
    font-family: "Intro Regular";
    font-weight: 400;
    font-size: 20px;
    line-height: 20px;
    color: var(--mine-shaft);
  }

  &__icon {
    display: block;
    width: 25px;
    height: 25px;
    position: relative;
    &::after {
      content: "";
      position: absolute;
      left: 50%;
      top: 0;
      width: 1px;
      height: 100%;
      background-color: var(--black);
      transition: transform 0.3s ease-in-out;
    }
    &::before {
      content: "";
      position: absolute;
      left: 50%;
      top: 0;
      width: 1px;
      height: 100%;
      background-color: var(--black);
      transform: rotate(90deg);
      transition: transform 0.3s ease-in-out;
    }
  }

  &__content {
    overflow: hidden;
    padding-bottom: 0px;
    max-height: 0;
    box-sizing: content-box;
    opacity: 0;
    will-change: max-height;
    transition: 0.3s ease-in-out;
  }

  &__text {
    margin: 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 140%;
    color: rgba(36, 36, 36, 0.5);
  }
}
.questions__item.open {
  .questions__content {
    opacity: 1;
    padding-bottom: 18px;
  }
  .questions__icon {
    &::before {
      transform: rotate(140deg);
    }
    &::after {
      transform: rotate(40deg);
    }
  }
}
