.team {
  background-color: var(--east-bay);
  position: relative;
  padding: 60px 0;
  &__container {
    position: relative;
    z-index: 2;
  }
  &__decor {
    position: absolute;
    right: 0;
    top: 0;
  }
  &__title {
    margin: 0;
    font-family: "Intro Bold";
    font-weight: 700;
    font-size: 30px;
    line-height: 30px;
    color: var(--light);
    margin-bottom: 40px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 40px 20px;
  }

  &__item {
    grid-column: 4 span;
  }

  &__img {
    margin-bottom: 20px;
    img {
      border-radius: 5px;
    }
  }

  &__name {
    margin: 0;
    font-family: "Intro Bold";
    font-weight: 700;
    font-size: 24px;
    line-height: 24px;
    color: var(--light);
    margin-bottom: 20px;
  }

  &__pos {
    margin: 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    color: var(--light);
  }
}
