.about {
  position: relative;
  background-color: var(--east-bay);
  &__decor {
    position: absolute;
    top: 0;
    left: 0;
  }
  &__container {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 30px;
    z-index: 2;
  }

  &__content {
    display: flex;
    flex-direction: column;
    max-width: 380px;
  }

  &__title {
    margin: 0;
    font-family: "Intro Bold";
    font-weight: 700;
    font-size: 30px;
    line-height: 30px;
    color: var(--light);
    margin-bottom: 20px;
  }

  &__text {
    margin: 0;
    color: var(--light);
    margin-bottom: 20px;
  }

  &__avtor {
    display: flex;
    align-items: center;
    font-family: "Intro Bold";
    font-weight: 700;
    font-size: 20px;
    line-height: 20px;
    color: var(--light);
    align-self: end;
    margin-right: -40px;
    &::before {
      content: "";
      width: 30px;
      height: 2px;
      background-color: var(--light);
      margin-right: 20px;
    }
  }

  &__img {
    clip-path: polygon(25% 0%, 100% 0%, 75% 100%, 0% 100%);
    img {
      display: block;
    }
  }
}
