.works {
  padding: 60px 0;
  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 30px;
  }
}
.works-questions {
  max-width: 380px;
  background-color: var(--east-bay);
  border-radius: 4px;
  padding: 40px 20px;
  &__title {
    margin: 0;
    font-family: "Intro Bold";
    font-weight: 700;
    font-size: 30px;
    line-height: 30px;
    color: var(--light);
    text-align: center;
    margin-bottom: 20px;
  }

  &__text {
    margin: 0 auto;
    max-width: 260px;
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    text-align: center;
    color: var(--light);
    margin-bottom: 40px;
  }

  &__label {
    width: 100%;
  }

  &__input {
    width: 100%;
    margin-bottom: 10px;
    padding: 18px 20px;
    border-radius: 4px;
    font-weight: 400;
    font-size: 16px;
    line-height: 140%;
    color: var(--gray);
    outline: none;
  }

  &__btn {
    width: 100%;
    background-color: var(--carnation);
    border-radius: 4px;
    padding: 22px;
    font-family: "Intro Regular";
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    text-align: center;
    color: var(--light);
  }
}
.instagram {
  display: flex;
  flex-direction: column;
  align-items: center;
  &__head {
    margin-bottom: 20px;
  }

  &__list {
    display: grid;
    gap: 14px 18px;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(2, 160px);
  }

  &__item {
    &:nth-child(3) {
      grid-row: 2 span;
      grid-column: 2 span;
    }
    &:nth-child(2) {
      grid-row: 2;
    }
  }

  &__img {
    img {
      width: 100%;
    }
  }
}
