.prod-modal {
  &__top {
    display: flex;
    padding: 20px;
    gap: 20px;
  }
  &__left {
    width: 50%;
  }
  &__right {
    width: 50%;
    display: flex;
    flex-direction: column;
  }
  &__descr {
    margin: 0;
    font-weight: 400;
    font-size: 24px;
    line-height: 24px;
    color: var(--river-bed);
    margin-bottom: 20px;
  }

  &__text {
    margin: 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 140%;
    color: var(--river-bed);
  }
  &__info {
    display: flex;
    gap: 47px;
    margin-bottom: 40px;
  }

  &__info-text {
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    color: var(--gray);
  }

  &__info-quantity {
    color: var(--river-bed);
  }

  &__title {
    margin: 0;
    font-weight: 400;
    font-size: 24px;
    line-height: 140%;
    color: var(--river-bed);
    margin-bottom: 20px;
  }

  &__star {
    margin-bottom: 20px;
  }

  &__btn {
    margin-top: auto;
    padding: 22px;
    width: 100%;
    font-family: "Intro Regular";
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    color: var(--light);
    background-color: var(--carnation);
    border-radius: 4px;
    margin-bottom: 20px;
  }
}
.modal-size {
  margin-bottom: 40px;
  &__title {
    margin: 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    color: var(--river-bed);
    margin-bottom: 20px;
  }

  &__list {
    display: flex;
    align-items: center;
    gap: 20px;
    flex-wrap: wrap;
  }

  &__btn {
    padding: 9px 26px;
    background-color: var(--light);
    border: 1px solid #b2b5bb;
    border-radius: 4px;
  }
}
.modal-price {
  display: flex;
  align-items: center;
  gap: 32px;
  &__nov {
    font-family: "Intro Bold";
    font-weight: 700;
    font-size: 30px;
    line-height: 30px;
    color: var(--river-bed);
  }

  &__old {
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    color: var(--gray);
  }
}
.modal-services {
  display: flex;
  flex-direction: column;
  gap: 10px;
  &__text {
    margin: 0;
    font-weight: 400;
    font-size: 14px;
    line-height: 14px;
    color: var(--gray);
    &::before {
      content: "";
      display: inline-block;
      width: 14px;
      height: 11px;
      background-image: url(../img/modal-check.svg);
      background-position: left center;
      background-repeat: no-repeat;
      margin-right: 10px;
    }
  }
}
.prod-slider {
  &__main {
    margin-bottom: 20px;
    .prod-slider__slide {
      img {
        height: 420px;
        width: 100%;
      }
    }
  }
  &__previem-item {
    cursor: pointer;
    img {
      width: 100%;
      height: 74px;
    }
  }
}
.prod-description {
  width: 50%;
}
.prod-сharacteristics {
  width: 50%;
  &__list {
    display: flex;
    flex-direction: column;
    gap: 11px;
  }

  &__text {
    margin: 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    color: var(--river-bed);
  }
}
.prod-video {
  iframe {
    width: 100%;
    height: 456px;
  }
}
