.contacts {
  padding: 60px 0;
  background: #f3f7f6;
  &__container {
    display: flex;
    justify-content: space-between;
    gap: 30px;
  }

  &__content {
    max-width: 365px;
  }

  &__title {
    margin: 0;
    font-family: "Intro Bold";
    font-weight: 700;
    font-size: 30px;
    line-height: 30px;
    color: var(--river-bed);
    margin-bottom: 60px;
  }

  &__item {
    &:not(:last-child) {
      margin-bottom: 40px;
    }
  }

  &__info {
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
    letter-spacing: 0.08em;
    text-transform: uppercase;
    color: var(--gray);
  }

  &__tel {
    font-family: "Intro Regular";
    font-weight: 400;
    font-size: 30px;
    line-height: 140%;
    color: var(--river-bed);
    margin-bottom: 10px;
  }

  &__text {
    margin: 0;
    font-family: "Intro Regular";
    font-weight: 400;
    font-size: 15px;
    line-height: 140%;
    color: var(--river-bed);
  }
  &__list {
    display: flex;
    align-items: center;
    gap: 20px;
  }
}
.tooltip {
  margin-left: 10px;
  position: relative;
  &__btn {
    width: 20px;
    height: 20px;
    background: var(--light);
    box-shadow: 2px 4px 10px rgba(0, 13, 84, 0.2);
    border-radius: 100%;
    text-align: center;
  }

  &__txt {
    position: absolute;
    left: -10px;
    bottom: calc(100% + 14px);
    padding: 10px 20px;
    z-index: 3;
    border-radius: 4px;
    width: max-content;
    max-width: 426px;
    box-shadow: 4px 8px 20px rgba(0, 13, 84, 0.2);
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
    color: var(--river-bed);
    background-color: var(--light);
    text-transform: initial;
    transform: translateY(10px);
    visibility: hidden;
    opacity: 0;
    transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out,
      visibility 0.3s ease-in-out;
  }
}
.tooltip__btn:hover + .tooltip__txt {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}

.tooltip__btn:focus + .tooltip__txt {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}
