.mini-cart {
  position: absolute;
  top: 45px;
  right: 0;
  z-index: 5;
  background-color: var(--light);
  box-shadow: 0px -4px 10px rgba(0, 13, 84, 0.1);
  width: 480px;
  &__list {
    display: flex;
    flex-direction: column;
    overflow: auto;
    max-height: 323px;
  }

  &__body {
    display: flex;
    justify-content: space-between;
    padding: 10px 20px;
  }

  &__info {
    display: flex;
    align-items: center;
    gap: 10px;
  }
  &__img {
    width: 100px;
    height: 100px;
  }
  &__descr {
    max-width: 268px;
  }

  &__title {
    margin: 0;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: var(--tundora);
    margin-bottom: 14px;
  }

  &__price {
    margin: 0;
    font-family: "Intro Bold";
    font-weight: 700;
    font-size: 20px;
    line-height: 20px;
    color: var(--tundora);
  }

  &__remove {
    svg {
      width: 20px;
      height: 20px;
      transition: color 0.4s ease-in-out;
      &:hover {
        fill: var(--carnation);
      }
    }
  }
}
.mini-cart-results {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 30px;
  padding: 20px;
  &__content {
    display: flex;
    flex-direction: column;
  }

  &__head {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: var(--tundora);
    margin-bottom: 10px;
  }

  &__price {
    font-family: "Intro Bold";
    font-weight: 700;
    font-size: 20px;
    line-height: 20px;
    color: var(--tundora);
  }

  &__btn {
    padding: 17px 47px;
    background-color: var(--carnation);
    border-radius: 4px;
    font-family: "Intro Regular";
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    color: var(--light);
  }
}
